import { visulizations } from "./visualizations";
export const slides = {
    "0": {
        visulization: "barChart",
        mounted: false,
        data: [2, 5, 4, 1, 2, 6, 5],
        onInit: function (selection) {
            const boundingRect = selection.node().getBoundingClientRect();
            this.width = boundingRect.width * 3 / 4;
            this.height = boundingRect.height;
            visulizations.barChart.init(selection, this.data, "steelblue", 0, this.height, 1);
        },
        onEnter: function () {
            if (!visulizations.barChart.chart)
                return;
            visulizations.barChart.chart.data(this.data);
            // const width = this.width || slides["1"].width;
            setTimeout(() => {
                if (visulizations.barChart.chart)
                    visulizations.barChart.chart.fillColor("steelblue");
                // if (visulizations.barChart.chart ) visulizations.barChart.chart.width(900);
            }, 250);
        },
        onExit: function () {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.exit();
        },
        onEnterScrollDown: function (ratio) {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.width(ratio * this.width);
        },
        onExitScrollDown: (ratio) => {
            console.log("scrolling down exit", ratio);
        },
        onEnterScrollUp: (ratio) => {
            console.log("scrolling up enter", ratio);
        },
        onExitScrollUp: function (ratio) {
            console.log("scrolling up exit", ratio);
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.width(ratio * this.width);
            if (ratio < .3 && visulizations.barChart.chart)
                visulizations.barChart.chart.width(0);
        },
    },
    "1": {
        visulization: "barChart",
        mounted: false,
        data: [17, 4, 18, 9, 2, 21, 88, 9, 12, 75, 33, 20, 18, 15, 17, 16, 18, 23],
        onInit: function (selection) {
            const boundingRect = selection.node().getBoundingClientRect();
            this.width = boundingRect.width;
            this.height = boundingRect.height;
            visulizations.barChart.init(selection, this.data, "coral", this.width, this.height, 1);
            this.mounted = true;
        },
        onEnter: function () {
            if (!visulizations.barChart.chart)
                return;
            visulizations.barChart.chart.data(this.data);
            const width = slides["0"].width;
            setTimeout(() => {
                if (visulizations.barChart.chart)
                    visulizations.barChart.chart.fillColor("coral");
                if (visulizations.barChart.chart && width)
                    visulizations.barChart.chart.width(width);
            }, 250);
        },
        onExit: function () {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.exit();
            this.mounted = false;
        },
        onEnterScrollDown: (ratio) => { },
        onExitScrollDown: (ratio) => {
            console.log("scrolling down exit", ratio);
        },
        onEnterScrollUp: (ratio) => {
            console.log("scrolling up enter", ratio);
        },
        onExitScrollUp: (ratio) => { },
    },
    "2": {
        visulization: "growingCircle",
        mounted: false,
        data: [0.5],
        onInit: function (selection) { },
        onEnter: () => { },
        onExit: () => { },
        onEnterScrollDown: (ratio) => {
            console.log("scrolling down enter", ratio);
        },
        onExitScrollDown: (ratio) => {
            console.log("scrolling down exit", ratio);
        },
        onEnterScrollUp: (ratio) => {
            console.log("scrolling up enter", ratio);
        },
        onExitScrollUp: (ratio) => {
            console.log("scrolling up exit", ratio);
        },
    },
};
